@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700&display=swap";
html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

html, body {
  overflow: -moz-scrollbars-vertical;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

html {
  font-size: 16.2px;
}

pre, code {
  font-family: Consolas, Menlo, Monaco, Andale Mono WT, Andale Mono, Lucida Console, Lucida Sans Typewriter, DejaVu Sans Mono, Bitstream Vera Sans Mono, Liberation Mono, Nimbus Mono L, Courier New, Courier, monospace;
  line-height: 1.5;
}

pre {
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  tab-size: 2;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  background-color: #f6f6f6;
  margin: .5em 0;
  padding: 1em;
  font-size: 1.6rem;
  line-height: 1.375;
}

code {
  word-break: break-all;
}

code[class*="language-"], pre[class*="language-"] {
  color: #ccc;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  background: none;
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  font-size: 1rem;
  line-height: 2.428rem;
}

pre[class*="language-"] {
  margin: auto;
  padding: 1.6rem;
  overflow: auto;
}

:not(pre) > code[class*="language-"], pre[class*="language-"] {
  background: #2e2e2e;
}

:not(pre) > code[class*="language-"] {
  white-space: normal;
  border-radius: .3em;
  padding: .1em;
}

.token.block-comment, .token.cdata, .token.comment, .token.doctype, .token.prolog {
  color: #999;
}

.token.punctuation {
  color: #ccc;
}

.token.attr-name, .token.deleted, .token.namespace, .token.tag {
  color: #e2777a;
}

.token.function-name {
  color: #6196cc;
}

.token.boolean, .token.function, .token.number {
  color: #e2777a;
}

.token.class-name, .token.constant, .token.property, .token.symbol {
  color: #9f9fff;
}

.token.atrule, .token.builtin, .token.important, .token.keyword, .token.selector {
  color: #cc99cd;
}

.token.attr-value, .token.char, .token.regex, .token.string, .token.variable {
  color: #4aecc1;
}

.token.entity, .token.operator, .token.url {
  color: #67cdcc;
}

.token.bold, .token.important {
  font-weight: 700;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.token.inserted {
  color: #4aecc1;
}

