mark {
  background-color: $secondary-color;
  color: $black-color;
}

.highlight {
  background-color: $secondary-color;
}

.avoid-clicks {
  pointer-events: none;
}

.tachado {
  text-decoration: line-through;
  text-underline-position: from-font;
  text-underline-offset: 0;
  text-decoration-thickness: 2px;
  text-decoration-color: $primary-color;
}

.rounded {
  border-radius: 50%;
}

.img--fl {
  float: left;
  margin-right: $size-3;
}

.img--fr {
  float: right;
  margin-left: $size-3;
}

.img--25vw {
  max-width: 25vw;
}

.img--50vw {
  max-width: 50vw;
}

.img--100vw {
  max-width: 100vw;
}
